'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { Plus, Trash2, X } from 'lucide-react';
import { cotacaoService } from '@/services/cotacaoService';
import { Produto, Fornecedor, CotacaoCreateDTO } from '@/types/cotação';

export default function CreateCotacaoPage() {
    const router = useRouter();
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState('');

    // Dados da cotação
    const [nome, setNome] = useState('');
    const [dataInicio, setDataInicio] = useState('');
    const [dataFim, setDataFim] = useState('');

    // Lista de produtos
    const [produtos, setProdutos] = useState<Produto[]>([]);
    const [showProdutoModal, setShowProdutoModal] = useState(false);
    const [novoProduto, setNovoProduto] = useState<Produto>({
        nome: '',
        descricao: '',
        unidade: '',
        quantidade: 1,
    });

    // Lista de fornecedores
    const [fornecedores, setFornecedores] = useState<Fornecedor[]>([]);
    const [showFornecedorModal, setShowFornecedorModal] = useState(false);
    const [novoFornecedor, setNovoFornecedor] = useState<Fornecedor>({
        nome: '',
        email: '',
        telefone: '',
        cnpj: '',
    });

    // Adicionar produto
    const handleAddProduto = () => {
        if (!novoProduto.nome || !novoProduto.unidade || novoProduto.quantidade <= 0) {
            alert('Preencha todos os campos obrigatórios do produto');
            return;
        }

        setProdutos([...produtos, { ...novoProduto, id: Date.now() }]);
        setNovoProduto({ nome: '', descricao: '', unidade: '', quantidade: 1 });
        setShowProdutoModal(false);
    };

    // Remover produto
    const handleRemoveProduto = (id: number) => {
        setProdutos(produtos.filter((p) => p.id !== id));
    };

    // Adicionar fornecedor
    const handleAddFornecedor = () => {
        if (!novoFornecedor.nome) {
            alert('Preencha o nome do fornecedor');
            return;
        }

        setFornecedores([...fornecedores, { ...novoFornecedor, id: Date.now() }]);
        setNovoFornecedor({ nome: '', email: '', telefone: '', cnpj: '' });
        setShowFornecedorModal(false);
    };

    // Remover fornecedor
    const handleRemoveFornecedor = (id: number) => {
        setFornecedores(fornecedores.filter((f) => f.id !== id));
    };

    // Submeter cotação
    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError('');

        if (!nome || !dataInicio || !dataFim) {
            setError('Preencha todos os campos da cotação');
            return;
        }

        if (produtos.length === 0) {
            setError('Adicione pelo menos um produto');
            return;
        }

        if (fornecedores.length === 0) {
            setError('Adicione pelo menos um fornecedor');
            return;
        }

        setLoading(true);

        try {
            const cotacao: CotacaoCreateDTO = {
                nome,
                dataInicio,
                dataFim,
                produtos,
                fornecedores,
            };

            await cotacaoService.postCriarCotacao(cotacao);
            router.push('/cotacoes');
        } catch (err) {
            setError('Erro ao criar cotação. Tente novamente.');
            console.error(err);
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="p-6 max-w-6xl mx-auto">
            <div className="mb-6">
                <h1 className="text-3xl font-bold text-gray-900">Nova Cotação</h1>
                <p className="text-gray-600 mt-2">Crie uma nova cotação adicionando produtos e fornecedores</p>
            </div>

            {error && (
                <div className="mb-4 p-4 bg-red-50 border border-red-200 rounded-lg text-red-700">
                    {error}
                </div>
            )}

            <form onSubmit={handleSubmit} className="space-y-6">
                {/* Informações básicas da cotação */}
                <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
                    <h2 className="text-xl font-semibold mb-4 text-gray-900">Informações da Cotação</h2>
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Nome da Cotação *
                            </label>
                            <input
                                type="text"
                                value={nome}
                                onChange={(e) => setNome(e.target.value)}
                                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                placeholder="Ex: Cotação de Materiais - Janeiro"
                                required
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Data de Início *
                            </label>
                            <input
                                type="date"
                                value={dataInicio}
                                onChange={(e) => setDataInicio(e.target.value)}
                                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                required
                            />
                        </div>
                        <div>
                            <label className="block text-sm font-medium text-gray-700 mb-2">
                                Data de Fim *
                            </label>
                            <input
                                type="date"
                                value={dataFim}
                                onChange={(e) => setDataFim(e.target.value)}
                                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                required
                            />
                        </div>
                    </div>
                </div>

                {/* Produtos */}
                <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
                    <div className="flex items-center justify-between mb-4">
                        <h2 className="text-xl font-semibold text-gray-900">Produtos</h2>
                        <button
                            type="button"
                            onClick={() => setShowProdutoModal(true)}
                            className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
                        >
                            <Plus size={20} />
                            Adicionar Produto
                        </button>
                    </div>

                    {produtos.length === 0 ? (
                        <p className="text-gray-500 text-center py-8">
                            Nenhum produto adicionado. Clique em "Adicionar Produto" para começar.
                        </p>
                    ) : (
                        <div className="overflow-x-auto">
                            <table className="w-full">
                                <thead className="bg-gray-50">
                                    <tr>
                                        <th className="px-4 py-3 text-left text-sm font-medium text-gray-700">Nome</th>
                                        <th className="px-4 py-3 text-left text-sm font-medium text-gray-700">Descrição</th>
                                        <th className="px-4 py-3 text-left text-sm font-medium text-gray-700">Unidade</th>
                                        <th className="px-4 py-3 text-left text-sm font-medium text-gray-700">Quantidade</th>
                                        <th className="px-4 py-3 text-center text-sm font-medium text-gray-700">Ações</th>
                                    </tr>
                                </thead>
                                <tbody className="divide-y divide-gray-200">
                                    {produtos.map((produto) => (
                                        <tr key={produto.id} className="hover:bg-gray-50">
                                            <td className="px-4 py-3 text-sm text-gray-900">{produto.nome}</td>
                                            <td className="px-4 py-3 text-sm text-gray-600">{produto.descricao || '-'}</td>
                                            <td className="px-4 py-3 text-sm text-gray-900">{produto.unidade}</td>
                                            <td className="px-4 py-3 text-sm text-gray-900">{produto.quantidade}</td>
                                            <td className="px-4 py-3 text-center">
                                                <button
                                                    type="button"
                                                    onClick={() => handleRemoveProduto(produto.id!)}
                                                    className="text-red-600 hover:text-red-800 transition-colors"
                                                >
                                                    <Trash2 size={18} />
                                                </button>
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    )}
                </div>

                {/* Fornecedores */}
                <div className="bg-white p-6 rounded-lg shadow-sm border border-gray-200">
                    <div className="flex items-center justify-between mb-4">
                        <h2 className="text-xl font-semibold text-gray-900">Fornecedores</h2>
                        <button
                            type="button"
                            onClick={() => setShowFornecedorModal(true)}
                            className="flex items-center gap-2 px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors"
                        >
                            <Plus size={20} />
                            Adicionar Fornecedor
                        </button>
                    </div>

                    {fornecedores.length === 0 ? (
                        <p className="text-gray-500 text-center py-8">
                            Nenhum fornecedor adicionado. Clique em "Adicionar Fornecedor" para começar.
                        </p>
                    ) : (
                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                            {fornecedores.map((fornecedor) => (
                                <div
                                    key={fornecedor.id}
                                    className="p-4 border border-gray-200 rounded-lg hover:border-gray-300 transition-colors relative"
                                >
                                    <button
                                        type="button"
                                        onClick={() => handleRemoveFornecedor(fornecedor.id!)}
                                        className="absolute top-2 right-2 text-red-600 hover:text-red-800 transition-colors"
                                    >
                                        <X size={18} />
                                    </button>
                                    <h3 className="font-semibold text-gray-900 mb-2">{fornecedor.nome}</h3>
                                    {fornecedor.email && (
                                        <p className="text-sm text-gray-600">Email: {fornecedor.email}</p>
                                    )}
                                    {fornecedor.telefone && (
                                        <p className="text-sm text-gray-600">Tel: {fornecedor.telefone}</p>
                                    )}
                                    {fornecedor.cnpj && (
                                        <p className="text-sm text-gray-600">CNPJ: {fornecedor.cnpj}</p>
                                    )}
                                </div>
                            ))}
                        </div>
                    )}
                </div>

                {/* Botões de ação */}
                <div className="flex gap-4 justify-end">
                    <button
                        type="button"
                        onClick={() => router.push('/cotacoes')}
                        className="px-6 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors"
                    >
                        Cancelar
                    </button>
                    <button
                        type="submit"
                        disabled={loading}
                        className="px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors disabled:bg-gray-400 disabled:cursor-not-allowed"
                    >
                        {loading ? 'Criando...' : 'Criar Cotação'}
                    </button>
                </div>
            </form>

            {/* Modal Adicionar Produto */}
            {showProdutoModal && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
                    <div className="bg-white rounded-lg p-6 max-w-md w-full">
                        <div className="flex items-center justify-between mb-4">
                            <h3 className="text-xl font-semibold text-gray-900">Adicionar Produto</h3>
                            <button
                                onClick={() => setShowProdutoModal(false)}
                                className="text-gray-500 hover:text-gray-700"
                            >
                                <X size={24} />
                            </button>
                        </div>
                        <div className="space-y-4">
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-2">
                                    Nome do Produto *
                                </label>
                                <input
                                    type="text"
                                    value={novoProduto.nome}
                                    onChange={(e) => setNovoProduto({ ...novoProduto, nome: e.target.value })}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                    placeholder="Ex: Cimento Portland"
                                />
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-2">
                                    Descrição
                                </label>
                                <textarea
                                    value={novoProduto.descricao}
                                    onChange={(e) => setNovoProduto({ ...novoProduto, descricao: e.target.value })}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                    placeholder="Descrição opcional do produto"
                                    rows={3}
                                />
                            </div>
                            <div className="grid grid-cols-2 gap-4">
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        Unidade *
                                    </label>
                                    <input
                                        type="text"
                                        value={novoProduto.unidade}
                                        onChange={(e) => setNovoProduto({ ...novoProduto, unidade: e.target.value })}
                                        className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                        placeholder="Ex: kg, un, m³"
                                    />
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        Quantidade *
                                    </label>
                                    <input
                                        type="number"
                                        value={novoProduto.quantidade}
                                        onChange={(e) => setNovoProduto({ ...novoProduto, quantidade: Number(e.target.value) })}
                                        className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                        min="1"
                                    />
                                </div>
                            </div>
                            <div className="flex gap-3 justify-end mt-6">
                                <button
                                    type="button"
                                    onClick={() => setShowProdutoModal(false)}
                                    className="px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50"
                                >
                                    Cancelar
                                </button>
                                <button
                                    type="button"
                                    onClick={handleAddProduto}
                                    className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
                                >
                                    Adicionar
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}

            {/* Modal Adicionar Fornecedor */}
            {showFornecedorModal && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
                    <div className="bg-white rounded-lg p-6 max-w-md w-full">
                        <div className="flex items-center justify-between mb-4">
                            <h3 className="text-xl font-semibold text-gray-900">Adicionar Fornecedor</h3>
                            <button
                                onClick={() => setShowFornecedorModal(false)}
                                className="text-gray-500 hover:text-gray-700"
                            >
                                <X size={24} />
                            </button>
                        </div>
                        <div className="space-y-4">
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-2">
                                    Nome do Fornecedor *
                                </label>
                                <input
                                    type="text"
                                    value={novoFornecedor.nome}
                                    onChange={(e) => setNovoFornecedor({ ...novoFornecedor, nome: e.target.value })}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                    placeholder="Ex: Construtora ABC"
                                />
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-2">
                                    Email
                                </label>
                                <input
                                    type="email"
                                    value={novoFornecedor.email}
                                    onChange={(e) => setNovoFornecedor({ ...novoFornecedor, email: e.target.value })}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                    placeholder="contato@fornecedor.com"
                                />
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-2">
                                    Telefone
                                </label>
                                <input
                                    type="tel"
                                    value={novoFornecedor.telefone}
                                    onChange={(e) => setNovoFornecedor({ ...novoFornecedor, telefone: e.target.value })}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                    placeholder="(00) 00000-0000"
                                />
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-2">
                                    CNPJ
                                </label>
                                <input
                                    type="text"
                                    value={novoFornecedor.cnpj}
                                    onChange={(e) => setNovoFornecedor({ ...novoFornecedor, cnpj: e.target.value })}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                    placeholder="00.000.000/0000-00"
                                />
                            </div>
                            <div className="flex gap-3 justify-end mt-6">
                                <button
                                    type="button"
                                    onClick={() => setShowFornecedorModal(false)}
                                    className="px-4 py-2 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50"
                                >
                                    Cancelar
                                </button>
                                <button
                                    type="button"
                                    onClick={handleAddFornecedor}
                                    className="px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700"
                                >
                                    Adicionar
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}
